  .react-stickies-wrapper {
    height: 100%;
    width: 100%;
    position: relative;
    margin-top: 20px;
  }
  
  /* Note styles */
  .react-stickies-wrapper .note-wrap {
    width: 100%;
    height: 100%;
    position: relative;
    vertical-align: top;
    display: inline-block;
    font-family: Englebert, Arial;
    color: #4b453c;
    background: #F7E999;
    line-height: 34px;
    text-align: center;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.1), 0 2px 4px 0 rgba(0,0,0,0.1);
  }
  .react-stickies-wrapper .note-wrap:before {
    display: block;
    content: "";
    width: 130px;
    height: 28px;
    border-radius: 6px/18px 0;
    position: absolute;
  }
  .react-stickies-wrapper .note-wrap.tape:before {
    background: rgba(227, 200, 114, 0.4);
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    top: -13px;
    left: 50px;
  }
  .react-stickies-wrapper .note-wrap .title {
    color: white;
    text-decoration: none;
    transition: all 0.4s ease;
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    left: 0;
  }
  .react-stickies-wrapper .note-wrap .title:hover {
    color: rgb(191, 191, 191);
  }
  
  .react-stickies-wrapper .note {
    background: #F7E999;
  }
  
  .react-stickies-wrapper .close {
    position: absolute;
    right: 10px;
    top: 10px;
    width: 20px;
    height: 20px;
    opacity: 0.3;
  }
  .react-stickies-wrapper .close:hover {
    opacity: 1;
    cursor: pointer;
  }
  .react-stickies-wrapper .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 16px;
    width: 2px;
    background-color: #333;
  }
  .react-stickies-wrapper .close:before {
    transform: rotate(45deg);
  }
  
  .react-stickies-wrapper .close:after {
    transform: rotate(-45deg);
  }
  
  .react-stickies-wrapper .add {
    position: absolute;
    left: 10px;
    top: 10px;
    width: 20px;
    height: 20px;
    opacity: 0.3;
    z-index: 2;
  }
  .react-stickies-wrapper .add:hover {
    opacity: 1;
    cursor: pointer;
  }
  .react-stickies-wrapper .add:before, .add:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 16px;
    width: 2px;
    background-color: #333;
  }
  .react-stickies-wrapper .add:before {
    transform: rotate(0deg);
  }
  .react-stickies-wrapper .add:after {
    transform: rotate(-90deg);
  }
  
