.react-stickies-wrapper * {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  }
  
  .react-stickies-wrapper::-moz-selection {
    text-shadow: none;
  }
  
  .react-stickies-wrapper::selection {
    text-shadow: none;
  }
  
  .react-stickies-wrapper .clearfix:before,
  .react-stickies-wrapper .clearfix:after {
    content: " ";
    display: table;
  }
  
  .react-stickies-wrapper .clearfix:after {
    clear: both;
  }
  
  .react-stickies-wrapper .clearfix {
    *zoom: 1;
  }
  
  
  .react-stickies-wrapper {
    height: 100%;
    width: 100%;
    position: relative;
    margin-top: 20px;
  }
  
  /* Note styles */
  .react-stickies-wrapper .note-wrap {
    width: 100%;
    height: 100%;
    position: relative;
    vertical-align: top;
    display: inline-block;
    font-family: Englebert, Arial;
    color: #4b453c;
    background: #F7E999;
    line-height: 34px;
    text-align: center;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.1), 0 2px 4px 0 rgba(0,0,0,0.1);
  }
  .react-stickies-wrapper .note-wrap:before {
    display: block;
    content: "";
    width: 130px;
    height: 28px;
    border-radius: 6px/18px 0;
    position: absolute;
  }
  .react-stickies-wrapper .note-wrap.tape:before {
    background: rgba(227, 200, 114, 0.4);
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    top: -13px;
    left: 50px;
  }
  .react-stickies-wrapper .note-wrap .title {
    color: white;
    text-decoration: none;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
    transition: all 0.4s ease;
    position: absolute;
    bottom: 0;
    text-align: center;
    width: 100%;
    left: 0;
  }
  .react-stickies-wrapper .note-wrap .title:hover {
    color: rgb(191, 191, 191);
  }
  
  .react-stickies-wrapper .note {
    background: #F7E999;
  }
  
  .react-stickies-wrapper .close {
    position: absolute;
    right: 10px;
    top: 10px;
    width: 20px;
    height: 20px;
    opacity: 0.3;
  }
  .react-stickies-wrapper .close:hover {
    opacity: 1;
    cursor: pointer;
  }
  .react-stickies-wrapper .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 16px;
    width: 2px;
    background-color: #333;
  }
  .react-stickies-wrapper .close:before {
    transform: rotate(45deg);
  }
  
  .react-stickies-wrapper .close:after {
    transform: rotate(-45deg);
  }
  
  .react-stickies-wrapper .add {
    position: absolute;
    left: 10px;
    top: 10px;
    width: 20px;
    height: 20px;
    opacity: 0.3;
    z-index: 2;
  }
  .react-stickies-wrapper .add:hover {
    opacity: 1;
    cursor: pointer;
  }
  .react-stickies-wrapper .add:before, .add:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 16px;
    width: 2px;
    background-color: #333;
  }
  .react-stickies-wrapper .add:before {
    transform: rotate(0deg);
  }
  .react-stickies-wrapper .add:after {
    transform: rotate(-90deg);
  }
  
  .react-stickies-wrapper .note-header {
    position: relative;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.1);
    height: 34px;
  }
  
  .react-stickies-wrapper .note-body {
    padding: 10px;
    font-size: 12px;
    color: white;
    line-height: 20px;
  }
  
  .react-stickies-wrapper .note-footer {
    color: white;
    text-decoration: none;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
    transition: all 0.4s ease;
    position: absolute;
    bottom: -6px;
    text-align: right;
    width: 100%;
    left: 0;
    padding-right: 10px;
    font-size: 9px;
  }


  .note-footerB {
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
    transition: all 0.4s ease;
    position: absolute;
    font-size: 9px;
    left: 8px;
    text-align: right;
    padding-right: 10px;
    bottom: 6px;
    z-index: 1;
    background-color:bisque;
    border-color: bisque;
    border-radius: 5px;
  }
  
  
  .react-stickies-wrapper [contenteditable]:focus {
      outline: 0px solid transparent;
  }
  
  
  
  /** Grid Styles **/
  .react-grid-dragHandleExample{
    cursor: move; /* fallback if grab cursor is unsupported */
    cursor: grab;
    cursor: -moz-grab;
    cursor: -webkit-grab;
  }
  
  .react-grid-layout {
    position: relative;
    transition: height 200ms ease;
  }
  .react-grid-item {
    transition: all 200ms ease;
    transition-property: left, top;
  }
  .react-grid-item.cssTransforms {
    transition-property: transform;
  }
  .react-grid-item.resizing {
    z-index: 1;
    will-change: width, height;
  }
  
  .react-grid-item.react-draggable-dragging {
    transition: none;
    z-index: 3;
    will-change: transform;
  }
  
  .react-grid-item.react-grid-placeholder {
    background: red;
    opacity: 0.2;
    transition-duration: 100ms;
    z-index: 2;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
  }
  
  .react-grid-item > .react-resizable-handle {
    position: absolute;
    width: 30px;
    height: 30px;
    bottom: 0px;
    right: 0px;
    background: url('data:image/svg+xml;base64,');
    background-position: bottom right;
    padding: 0 3px 3px 0;
    background-repeat: no-repeat;
    background-origin: content-box;
    box-sizing: border-box;
    cursor: se-resize;
  }
  